import "../Topbar/Topbar.css"
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from "react-router-dom"
export class Topbar extends Component {
    render() {
        return (
            <div className="top-bar">
                <div className="logo">
                    <a ><Link to={""}><h4><span>SHOP</span>LANE</h4></Link></a>
                </div>
                <ul className="nav-links">
                    <li><Link to={""}>Home</Link></li>
                    <li><a href="#clothing">Clothings</a></li>
                    <li><a href="#accessories">Accessories</a></li>
                </ul>
                <div className="icons">
                    <i className="fa fa-search"></i>
                    <Link to="/cart" style={{ textDecoration: 'none' }}>
                        <div className="cart" className="cartIcon">
                            <a>
                                <i className="fa fa-shopping-cart">
                                    <sup className="cart-count">{this.props.cartCount.length}</sup>
                                </i>
                            </a>
                        </div>
                    </Link>
                    <i className="fa fa-user-circle-o"></i>
                </div>
                <div className="burger">
                    <div className="line1"></div>
                    <div className="line2"></div>
                    <div className="line3"></div>
                </div>

            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    cartCount: state.CartData.CartDataArray
})


export default connect(mapStateToProps, null)(Topbar)