import "../Cart/cart.css";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
export class Cart extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: []
    }
  }

  componentDidMount() {
    let z = this.props.ReceivecartData
    if (z.length !== 0) {
      z.sort(function (a, b) {
        var keyA = a.id,
          keyB = b.id;
        // Compare the 2 ids
        if (keyA < keyB) return -1;
        if (keyA > keyB) return 1;
        return 0;
      });

      let newarr = [];
      let counter = 1;
      for (let i = 0; i < z.length; i++) {
        if (i === z.length - 1) {
          if (z[i] !== z[i - 1]) {
            z[i].quantity = 1;
            newarr.push(z[i]);
          } else {
            z[i].quantity = counter;
            newarr.push(z[i]);
          }
        } else {
          if (z[i].id === z[i + 1].id) counter++;
          else {
            z[i].quantity = counter;
            newarr.push(z[i]);
            counter = 1;
          }
        }
      }
      console.log("original array", z);
      console.log("new array ", newarr);
      this.setState({ data: newarr })
    } else console.log("no data from store");

  }

  totalAmount = () => {
    let y = this.state.data
    let amount = 0;
    for (let i = 0; i < y.length; i++) {
      amount += (y[i].price * y[i].quantity)
    }
    return amount;
  }

  render() {
    return (
      <section id="cart">
        <h1>Checkout</h1>
        <div className="cart-container">
          <div className="left-side">
            <p>
              Total Items: <span id="number-of-item">{(this.state.data.length)}</span>
            </p>

            <div className="scrol">

              <div className="cart-items" id="cart-item-container">
                {this.state.data.map(item => <CartCard {...item} />)}
              </div>
            </div>

          </div>


          <div className="right-side">
            <div className="total-amount">
              <h2>Total Amount</h2>
              <p>
                Total Amount: <b>Rs.</b> <span id="total-amount">{this.totalAmount()}</span>
              </p>
              {this.state.data.length !== 0 ? (
                <Link to={"/checkout"} style={{ textDecoration: 'none' }}>
                  <button id="place-order"
                  >Place Order</button>
                </Link>) : ""}

            </div>
          </div>
        </div>
      </section>
    );
  }
}

const mapStateToProps = (state) => ({
  ReceivecartData: state.CartData.CartDataArray,
});

export default connect(mapStateToProps, null)(Cart);

const CartCard = ({ preview, name, quantity, price }) => {
  return (
    <div className="item">
      <img src={preview} />
      <div className="detail">
        <h3>{name}</h3>
        <p>x{quantity}</p>
        <p>Amount: {price * quantity}</p>
      </div>
    </div>
  );
}


